import React from "react";
import "../../src/App.css";

const Syllabus = () => {
    const categories = [
        {
            title: "Backend (with Java)",
            items: [
                "Programming in Core Java",
                "Advanced Java",
                "Enterprise Java",
                "Testing",
                "Deployment",
                "Tools",
                "Essential Java for Competitive Programming",
                "Microservices",
                "Micro-frontend",
                "Additional Concepts",
                "Performance optimisation",
            ],
        },
        {
            title: "Backend (with JavaScript)",
            items: [
                "NodeJs",
                "ExpressJs",
                "Authentication & Authorisation",
                "Websockets",
            ],
        },
        {
            title: "Security",
            items: ["Web security", "Https & CORS"],
        },
        {
            title: "Computer Science & Applications",
            items: [
                "DS in Java",
                "Algorithms in Java",
                "Databases",
                "System Design",
                "Computer Organization & Architecture",
                "Compiler Design",
                "Software Engineering",
                "Operating Systems",
                "Computer Networks",
            ],
        },
        {
            title: "Frontend",
            items: [
                "Core Concepts",
                "Advanced JavaScript",
                "State Management",
                "API integration",
                "Build tools and environment",
                "Testing and debugging",
                "Performance optimisation UI",
                "UI/UX design principles",
                "Version control and collaboration",
            ],
        },
        {
            title: "DevOps",
            items: ["AWS", "CI/CD", "Containerization", "Version Control", "Kubernetes"],
        },
        {
            title: "Behavioral",
            items: ["Softskills"],
        },
    ];

    return (
        <div style={{ padding: "20px", fontFamily: "'Poppins', sans-serif" }}>
            <h1 style={{ textAlign: "center", color: "#007bff", marginBottom: "30px" }}>
                Topics Overview
            </h1>
            <div
                style={{
                    display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, minmax(320px, 1fr))",
                    gap: "20px",
                }}
            >
                {categories.map((category, index) => (
                    <div
                        key={index}
                        style={{
                            backgroundColor: "#fff",
                            borderRadius: "15px",
                            boxShadow: "0 10px 20px rgba(0, 0, 0, 0.15)",
                            padding: "20px",
                            transition: "transform 0.3s ease, box-shadow 0.3s ease",
                            cursor: "pointer",
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style.transform = "translateY(-5px)";
                            e.currentTarget.style.boxShadow = "0 15px 25px rgba(0, 0, 0, 0.2)";
                        }}
                        onMouseLeave={(e) => {
                            e.currentTarget.style.transform = "translateY(0)";
                            e.currentTarget.style.boxShadow = "0 10px 20px rgba(0, 0, 0, 0.15)";
                        }}
                    >
                        <h2
                            style={{
                                color: "#007bff",
                                fontSize: "1.5em",
                                marginBottom: "10px",
                                textAlign: "center",
                                borderBottom: "2px solid #007bff",
                                paddingBottom: "5px",
                            }}
                        >
                            {category.title}
                        </h2>
                        <ul style={{ listStyle: "none", padding: 0, margin: 0 }}>
                            {category.items.map((item, idx) => (
                                <li
                                    key={idx}
                                    style={{
                                        marginBottom: "8px",
                                        color: "#333",
                                        fontSize: "1em",
                                        lineHeight: "1.6",
                                        padding: "5px 10px",
                                        borderRadius: "5px",
                                        backgroundColor: "#f9f9f9",
                                        transition: "background-color 0.3s ease",
                                    }}
                                    onMouseEnter={(e) =>
                                        (e.currentTarget.style.backgroundColor = "#e6f2ff")
                                    }
                                    onMouseLeave={(e) =>
                                        (e.currentTarget.style.backgroundColor = "#f9f9f9")
                                    }
                                >
                                    {item}
                                </li>
                            ))}
                        </ul>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Syllabus;
